body {
  font-family: Arial, serif;
  font-size: 16px;
  line-height: 18px;
}
body .header__nav .container .row .col-lg-1 ul li a {
  padding: 0 5px;
  display: flex;
  align-items: center;
  position: relative;
  font: 0.875em/50px "Arial";
  color: #777a79;
  text-decoration: none;
  z-index: 10;
}
body .header__nav .container .row .col-lg-1 ul li ul {
  display: none;
  flex-direction: column;
  position: absolute;
  top: 40px;
  background: #fff;
  z-index: 10;
}
body .header__nav .container .row .col-lg-1 ul li ul li a {
  line-height: 30px;
}
body .header__nav .container .row .col-lg-1 .language li {
  position: relative;
}
body .header__nav .container .row .col-lg-1 .language li:hover ul {
  display: flex;
}
body .header__nav .container .row .col-lg-5 ul {
  display: flex;
  justify-content: space-between;
}
body .header__nav .container .row .col-lg-5 ul li a {
  display: flex;
  align-items: center;
  font: 0.875em/50px "Arial";
  color: #777a79;
  text-decoration: none;
}
body .header__nav .container .row .col-lg-5 ul li a:hover {
  text-decoration: underline;
}
body .header__nav .container .row .col-lg-5 ul li ul {
  width: 100px;
  display: none;
  flex-direction: column;
  position: absolute;
  top: 40px;
  background: #fff;
  z-index: 10;
}
body .header__nav .container .row .col-lg-5 ul li ul li {
  text-align: center;
}
body .header__nav .container .row .col-lg-5 ul li ul li a {
  display: inline-block;
  line-height: 30px;
}
body .header__nav .container .row .col-lg-5 .nav li {
  position: relative;
}
body .header__nav .container .row .col-lg-5 .nav li:hover ul {
  display: flex;
}
body .header__nav .container .row .col-lg-5 .mobile__nav {
  display: none;
  position: relative;
  left: -15px;
  text-align: center;
}
body .header__nav .container .row .col-lg-5 .mobile__nav #mobileNav,
body .header__nav .container .row .col-lg-5 .mobile__nav #mobileNavClose {
  font-size: 1.5em;
  line-height: 50px;
  color: #777a79;
  text-decoration: none;
}
body .header__nav .container .row .col-lg-5 .mobile__nav ul {
  width: 200px;
  flex-direction: column;
  position: absolute;
  top: 50px;
  right: -35px;
  background-color: #fff;
  box-shadow: 5px 5px 15px #c4c4c4;
  z-index: 20;
}
body .header__nav .container .row .col-lg-5 .mobile__nav ul li a {
  padding: 0 0 0 25px;
  border-top: 1px solid #c4c4c4;
  border-bottom: 1px solid #c4c4c4;
}
body .header__popups {
  display: none;
}
body .header__headline {
  height: 135px;
  background-color: #e6e6e6;
}
body .header__headline .container .row .col-lg-12 p {
  width: 100%;
  height: 135px;
  position: relative;
  background: url("../img/SH.png") no-repeat;
  background-size: 100%;
}
body .header__headline .container .row .col-lg-6 .logo {
  width: 100%;
  height: 135px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -135px;
  z-index: 5;
}
body .header__headline .container .row .col-lg-6 .logo a {
  width: 310px;
  height: 135px;
  display: block;
  background-image: url("../img/logo.png");
  background-size: 100% 100%;
}
body .header__headline .container .row .col-lg-6 .logo div {
  position: relative;
  left: -15px;
}
body .header__headline .container .row .col-lg-6 .logo div p:nth-child(1) {
  margin: 0 0 0 20px;
  font: 1.188em/1em "BravoSC";
  color: #00548f;
  text-align: left;
}
body .header__headline .container .row .col-lg-6 .logo div p:nth-child(2) {
  font: 1.5em/1em "BravoSC";
  color: #00548f;
  text-align: left;
}
body .header__headline .container .row .col-lg-6 .search {
  width: 100%;
  height: 135px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: -135px;
  left: -15px;
  z-index: 5;
}
body .header__headline .container .row .col-lg-6 .search input {
  width: 460px;
  height: 33px;
  padding: 0 0 0 15px;
  font: 0.875em/33px "Arial";
  color: #9fa1a2;
  background-color: #f5f5f5;
  border: 1px solid #c4c4c4;
}
body .header__headline .container .row .col-lg-6 .search i {
  width: 37px;
  height: 33px;
  line-height: 33px;
  text-align: center;
  background: #fff;
  border-top: 1px solid #c4c4c4;
  border-right: 1px solid #c4c4c4;
  border-bottom: 1px solid #c4c4c4;
}
body .header__mainNav {
  background-color: #009fff;
}
body .header__mainNav .container .row .col-lg-12 ul {
  display: flex;
  justify-content: space-between;
}
body .header__mainNav .container .row .col-lg-12 ul li {
  width: 100%;
  position: relative;
}
body .header__mainNav .container .row .col-lg-12 ul li a {
  height: 60px;
  padding: 0 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  text-decoration: none;
}
body .header__mainNav .container .row .col-lg-12 ul li a span {
  display: block;
  font: 0.813em/1.063em "Arial";
  text-align: center;
  text-transform: uppercase;
  white-space: nowrap;
}
body .header__mainNav .container .row .col-lg-12 ul li a span:nth-child(1) {
  font-weight: bold;
  color: #fff;
}
body .header__mainNav .container .row .col-lg-12 ul li a span:nth-child(2) {
  color: #9fdbff;
}
body .header__mainNav .container .row .col-lg-12 ul li a:hover {
  background-color: #007eec;
}
body .header__mainNav .container .row .col-lg-12 ul .active {
  background-color: #007eec;
}
body .header__mainNav .container .row .col-lg-12 ul .active:after {
  content: "";
  width: 100%;
  height: 0;
  position: absolute;
  top: 100%;
  left: 0;
  border: 90px solid transparent;
  border-top: 18px solid #007eec;
}
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav {
  display: none;
  justify-content: space-between;
  position: relative;
}
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav #mobileMainNav,
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav #mobileMainNavClose {
  padding: 0 5px;
  font-size: 1.5em;
  line-height: 60px;
  color: #fff;
  text-decoration: none;
}
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav #mobileMainNav:hover,
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav #mobileMainNavClose:hover {
  cursor: pointer;
}
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav ul {
  width: 300px;
  flex-direction: column;
  position: absolute;
  top: 60px;
  left: -15px;
  background-color: #009fff;
  z-index: 20;
}
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav .search {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav .search input {
  width: 90%;
  height: 33px;
  padding: 0 0 0 15px;
  font: 0.875em/33px "Arial";
  color: #9fa1a2;
  background-color: #f5f5f5;
  border: 1px solid #c4c4c4;
}
body .header__mainNav .container .row .col-lg-12 .mobile__mainNav .search i {
  width: 37px;
  height: 33px;
  line-height: 33px;
  text-align: center;
  background: #fff;
  border-top: 1px solid #c4c4c4;
  border-right: 1px solid #c4c4c4;
  border-bottom: 1px solid #c4c4c4;
}
body #signInPopup,
body #logInPopup {
  padding: 15px;
  width: 315px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
}
body #signInPopup h6,
body #logInPopup h6 {
  width: 100%;
  font: 1.25em/2em "Arial";
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  background-color: #e6e6e6;
}
body #signInPopup form ul,
body #logInPopup form ul {
  width: 100%;
}
body #signInPopup form ul li,
body #logInPopup form ul li {
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
body #signInPopup form ul li p,
body #logInPopup form ul li p {
  font: 0.875em/1.25em "Arial";
  color: #525252;
  white-space: nowrap;
}
body #signInPopup form ul li .important,
body #logInPopup form ul li .important {
  font-weight: bold;
}
body #signInPopup form ul li label,
body #logInPopup form ul li label {
  display: flex;
}
body #signInPopup form ul li label span,
body #logInPopup form ul li label span {
  font-size: 1.75em;
  line-height: 25px;
  color: #ff5a5a;
}
body #signInPopup form ul li label input,
body #logInPopup form ul li label input {
  padding: 0 0 0 5px;
  width: 180px;
  height: 25px;
  font: 0.875em/25px "Arial";
  color: #525252;
  border: 1px solid #c7c7c7;
  outline: none;
}
body #signInPopup form ul li label .error,
body #logInPopup form ul li label .error {
  border-color: #e06565;
}
body #signInPopup form button,
body #logInPopup form button {
  margin: 20px 0 0;
  width: 100%;
  display: block;
  font: 1.125em/35px "Arial";
  font-weight: bold;
  color: #fff;
  text-align: center;
  text-decoration: none;
  background-color: #009fff;
  border: none;
  outline: none;
}
body #signInPopup form button:hover,
body #logInPopup form button:hover {
  background-color: #007eec;
}
